/* Fullscreen wrapper */
.splash-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #330287;
  }
  
  /* Logo styling */
  .logo-image {
    width: 3cm; /* Adjust size as needed */
    height: auto;
  }
  
  /* Text below logo */
  .logo-text {
    color: white;
    font-size: 2.5rem;
    margin-top: 20px;
    font-family: 'Arial', sans-serif;
    text-transform: uppercase;
    letter-spacing: 5px;
  }
  
  /* Optional: Adding a fade-in animation */
  .splash-wrapper {
    animation: fadeIn 1.5s ease-in-out;
  }
  
  @keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
  }
  